<template>
    <v-container>
        <v-row class="mt-5" justify="center">
            <v-col cols="6">
                <h2>Создание заказа</h2>
                <v-form>
                    <v-text-field
                            v-model="name"
                            label="Название"
                    ></v-text-field>
                    <v-autocomplete
                            v-model="account"
                            :items="accounts"
                            :loading="isLoading"
                            :search-input.sync="search"
                            hide-no-data
                            hide-selected
                            item-text="email"
                            item-value="email"
                            label="Пользователь"
                            return-object
                            :rules="[v => !!v || 'Обязательное поле']"
                    ></v-autocomplete>
                    <v-select
                            v-model="plan"
                            :items="plans"
                            item-text="name"
                            item-value="id"
                            label="План"
                            :rules="[v => !!v || 'Обязательное поле']"
                    ></v-select>
                    <v-text-field
                            v-model="provider_id"
                            label="ID провадйера"
                    ></v-text-field>
                    <v-text-field
                            v-model="amount"
                            type="number"
                            label="Количество"
                            :rules="[v => !!v || 'Обязательное поле']"
                            required
                    ></v-text-field>
                    <v-select
                            v-model="status"
                            :items="statuses"
                            item-text="name"
                            item-value="id"
                            label="Статус"
                    ></v-select>

                    <v-textarea
                            v-model="comment"
                            label="Комментарий"
                    ></v-textarea>
                    <v-btn @click="submit" :disabled="!account || !plan || !amount" class="primary">
                        Создать заказ
                    </v-btn>
                </v-form>
            </v-col>
        </v-row>
        {{status}}
    </v-container>
</template>

<script>
    import {mapActions} from 'vuex'
    export default {
        name: "AddOrder",
        data: () => ({
            account: null,
            accounts: [],
            isLoading: false,
            search: null,
            plan: null,
            plans: [],
            amount: null,
            provider_id: null,
            name: null,
            comment: null,
            status: null,
            statuses: []
        }),

        mounted() {
            this.getPlans()
            this.getStatuses()
        },

        methods: {
            ...mapActions(['setAlert']),
            async getPlans() {
                const res = await this.axios({url: `${process.env.VUE_APP_SERVER_URL}api/v1/plans/`, method: 'GET'})
                this.plans = res.data
            },
            async getStatuses() {
                const res = await this.axios({url: `${process.env.VUE_APP_SERVER_URL}api/v1/orders/statuses`, method: 'GET'})
                this.statuses = res.data
            },
            submit() {
                const data = {
                    user: this.account.id,
                    plan: this.plan,
                    amount: this.amount
                }
                if (this.name) data['name'] = this.name
                if (this.provider_id) data['provider_id'] = this.provider_id
                if (this.comment) data['comment'] = this.comment
                if (this.status) data['status'] = this.status
                this.axios({
                    url: `${process.env.VUE_APP_SERVER_URL}api/v1/orders/`, method: 'POST', data
                }).then(res => {
                    this.setAlert({
                        active: true,
                        color: 'success',
                        text: 'Заказ создан'
                    })
                    this.$router.push(`/orders/${res.data.id}`)
                }).catch(err => {
                    this.setAlert({
                        active: true,
                        color: 'error',
                        text: err.response.data
                    })
                })
            }
        },

        watch: {
            search() {
                // Items have already been loaded
                if (this.accounts.length > 0) return

                // Items have already been requested
                if (this.isLoading) return

                this.isLoading = true

                // Lazily load input items
                this.axios({
                    url: `${process.env.VUE_APP_SERVER_URL}api/v1/users`,
                    method: 'GET'
                })
                    .then(res => {
                        this.accounts = res.data
                    })
                    .catch(err => {
                        console.log(err)
                    })
                    .finally(() => (this.isLoading = false))
            },
        }
    }
</script>

